import React from 'react'
import Logo from '../images/logo.png'
import Menu from '../images/menu.svg'

const Header = () => {
  return (
    <header className='header'>
        <nav className='header-nav'>
            <a href="http://sch2.glusk.edu.by/ru/main.aspx"><img className='logo-img' src={Logo} alt="" width="82" height="101"/></a>
            <ul className='nav-links'>
                <li className='nav-item'><a href='/'></a></li>
                <li className='nav-item'><a href='/info'></a></li>
                <li className='nav-item'><a href='/about'> </a></li>
            </ul>
            <button className='menu_btn'>
                <img className='menu-img' src={Menu} alt='' width="52" height="25"/>
            </button>
            <div className='menu_wrapper'>
                <ul>
                    {/* <li><a href='#'>    </a></li> */}
                    <li><a href='/memory'>  </a></li>
                    {/* <li><a href='#'> </a></li>
                    <li><a href='#'> - </a></li> */}
                    <li><a href='/monuments'>  </a></li>
                    <li><a href='/child-memory'> </a></li>
                    <li><a href='/holocost'> </a></li>
                    <li><a href='/teachers'> - </a></li>
                    {/* <li><a href='#'>  </a></li> */}
                </ul>
            </div>
        </nav>
    </header>
  )
}

export default Header